export default "import Table from \"af-react-table/lib/Table\";\nimport times from \"lodash/times\";\nimport range from \"lodash/range\";\nimport r from \"lodash/random\";\nimport { css } from \"@emotion/core\";\n\nconst wrapperCss = css`\n    table {\n        border-spacing: 0;\n    }\n    tfoot td, th {\n        padding: 0.5em;\n        background: #e6e6e6;\n        font-weight: bold;\n    }\n`;\n\nconst colCount = 5;\n\nconst getRowExtraProps = ( rowData, rowDataIndex ) => rowDataIndex % 2 ? { \"data-odd\": \"\" } : {};\n\nconst columns = times( colCount, colIndex => ({\n    dataKey: `col${colIndex}`,\n    label: `col${colIndex}`,\n    background: `rgb(${r(130,220)}, ${r(130,220)}, ${r(130,220)})`,\n    width: r( 50, 300 )\n}));\n\nconst getRowData = index => range( colCount ).reduce(( acc, colIndex ) => {\n    acc[ `col${colIndex}` ] = index;\n    return acc;\n}, {});\n\nconst totals = range( colCount ).reduce(( acc, colIndex ) => {\n    acc[ `col${colIndex}` ] = [ \"count\" ];\n    return acc;\n}, {});\n\nconst TableWithStyledColumns = () => (\n    <Table\n        getRowExtraProps={getRowExtraProps}\n        css={wrapperCss}\n        useStickyIfPossible\n        getRowData={getRowData}\n        rowCount={500}\n        totals={totals}\n        columns={columns}\n    />\n);\n\nexport default TableWithStyledColumns;";